import React from 'react'
import '../css/faq.css'
import BigButton from '../uikit/BigButton'
import Question from '../uikit/Question'
import { useState } from 'react'

const Faq = ({ onChangePage }) => {

    const questions = [
        {
            text: "Cmo empezar a jugar?",
            answer: `
                <p class="accountmenu__content__cards__answer__text">
                Lo primero que debe hacer es llegar al edificio del gobierno y comenzar su viaje.<br><br>
                Abra la pestaa "Misiones" y vaya a la lnea de bsqueda "Primeros pasos".<br><br>
                </p>
                <ol>
                <li class="accountmenu__content__cards__answer__li">Nuestra pgina web: ciudadlatina.net</li>
                </ol>
            `
        },
        {
            text: "Cmo funciona el programa de referidos?",
            answer: `
                <p class="accountmenu__content__cards__answer__text">
                Despus de que su amigo ingrese su apodo en el campo al registrar un personaje (Espacio por ejemplo: Alexey Navalniy), recibir una bonificacin en forma de Punto de bonificacin en su cuenta personal. Gracias a estos puntos de bonificacin, puede utilizar los servicios de donacin o cambiarlos por moneda del juego. Despus de que tu amigo juegue 24 horas en el servidor, recibir un bono de $20 000 y 100 pb, y t recibirs 500 pb en la cuenta de tu personaje.<br><br>
                Se puede ver una lista de todas sus referencias en el sitio, en las estadsticas de caracteres<br><br>
                </p>
                <ol>
                <li class="accountmenu__content__cards__answer__li">Nuestra pgina web: ciudadlatina.net</li>
                </ol>
            `
        },
        {
            text: "Cmo conseguir trabajo?",
            answer: `
                <p class="accountmenu__content__cards__answer__text">
                Puedes conseguir un trabajo en el edificio del gobierno en la seccin "trabajo".<br><br>
                Los trabajos estn disponibles a medida que aumenta el nivel del trabajador. Se puede actualizar ganando experiencia mientras se trabaja. Puede realizar un seguimiento de la progresin de la carrera en las estadsticas.
                <br><br>Lista de trabajos disponibles:
                </p>
                <ol>
                <li class="accountmenu__content__cards__answer__li">Nivel 1  Manitas, jardinero</li>
                <li class="accountmenu__content__cards__answer__li">Nivel 2  Conductor de autobs, taxista</li>
                <li class="accountmenu__content__cards__answer__li">Nivel 3  Cartero, fotgrafo, mecnico</li>
                <li class="accountmenu__content__cards__answer__li">Nivel 4  Coleccionista, camionero en una furgoneta</li>
                <li class="accountmenu__content__cards__answer__li">Nivel 8  Conductor de camin</li>
                <li class="accountmenu__content__cards__answer__li">Nivel 12  Camionero en un camin con remolque</li>
                </ol>
            `
        },
        {
            text: "Dnde comprar licencias?",
            answer: `
                <p class="accountmenu__content__cards__answer__text">
                Las licencias de vehculos se pueden comprar en el centro de licencias ubicado en el edificio del gobierno. Para comprar una licencia comercial, de caza o de pesca, comunquese con los funcionarios del gobierno.
                <br><br>
                                    Las licencias de armas son emitidas por oficiales de LSPD y BCSD. Pero primero necesitas un seguro mdico, que se puede comprar en una farmacia o de los empleados de EMS.
                                </p>
            `
        },
        {
            text: "Dnde comprar tarjetas bancarias?",
            answer: `
                <p class="accountmenu__content__cards__answer__text">
                Las tarjetas bancarias se compran en cualquier sucursal bancaria de Fleeca, Maze, Blaine o Pacific. Entre ellos, solo difieren en la ubicacin y la comisin por retirar fondos de un cajero automtico. Pero al comienzo del juego, puedes elegir cualquier banco, no te preocupes.
                </p>
            `
        },
        {
            text: "Cmo obtener servicios de donacin?",
            answer: `
                <p class="accountmenu__content__cards__answer__text">
                Los servicios de donacin se pueden utilizar en la seccin "Donar" del sitio web ciudadlatina.net
                </p>
            `
        },
        {
            text: "Cmo denunciar a un jugador o administrador?",
            answer: `
                <p class="accountmenu__content__cards__answer__text">
                Puedes dejar un reporte sobre un jugador o un administrador en el sitio <a>ciudadlatina.net</a> en la seccin "Reporte".
                </p>
            `
        },
        {
            text: "Todo sobre el vehculo",
            answer: `
                <p class="accountmenu__content__cards__answer__text">
                Hay 10 espacios para vehculos en total. Puedes tener hasta 5 autos sin casa, los 5 cupos restantes se adquieren en el sitio web <a>ciudadlatina.net</a> en la seccin "Donar".
                </p>
                <br><br>
                <h1>Donde comprar un vehculo?</h1><br>
                <p class="accountmenu__content__cards__answer__text">
                Los concesionarios de automviles estn marcados en el mapa con un cono de garaje con un dlar o en el telfono en la pestaa GPS. El ms barato est en Sandy Shores, donde puedes comprar vehculos hasta $2000
                </p>
                <br><br>
                <h1>Cmo interactuar con el vehculo?</h1><br>
                <p class="accountmenu__content__cards__answer__text">
                Abra el telfono (por defecto con la tecla O) y ejecute la aplicacin UVeh.<br><br>
                Presione L (estndar) para abrir/cerrar el transporte.<br><br>
                Para arrancar/detener el motor, presione B (estndar).
                </p>
                <br><br>
                <h1>Cuanta velocidad me da el tuning de autos?</h1><br>
                <p class="accountmenu__content__cards__answer__text">
                Turbo otorga +10 a la velocidad del vehculo y 4 motores de nivel +20, en total, cuando el automvil est completamente actualizado, obtendr +30 a la velocidad mxima
                </p>
                <br><br>
                <h1>Cmo robar el vehculo de otro jugador? Y que debo hacer con el?</h1><br>
                <p class="accountmenu__content__cards__answer__text">
                Puedes robar vehculos usando ganzas o hackeando la consola. Puede entregar el automvil a travs de la consola usando comandos especiales.
                </p>
            `
        },
        {
            text: "Cmo comprar una casa?",
            answer: `
                <p class="accountmenu__content__cards__answer__text">
                Hay dos formas de comprar una casa:
                </p>
                <ol>
                <li class="accountmenu__content__cards__answer__li">Seleccione la casa deseada y, si est libre, vaya a la entrada de la casa, presione [E] y compre.</li>
                <li class="accountmenu__content__cards__answer__li">Si la casa pertenece a otro jugador, cmprele la vivienda de sus manos por su precio establecido en el edificio del gobierno, en la seccin "Propiedad".</li>
            
                </ol>
            `
        },
        {
            text: "Cmo funcionan los impuestos a la propiedad?",
            answer: `
                <p class="accountmenu__content__cards__answer__text">
                Los impuestos se aplican a todo su transporte y alojamiento. Puede obtener informacin sobre impuestos y pagarlos en el edificio del gobierno, en la seccin "Oficina de impuestos".
                </p>
            `
        },
        {
            text: "Cmo acelerar la nivelacin de habilidades?",
            answer: `
                <p class="accountmenu__content__cards__answer__text">
                Corre, lucha, dispara o nada con ms frecuencia. Si desea actualizar sus habilidades de inmediato, puede comprar el servicio correspondiente en la seccin "Donar" del sitio web ciudadlatina.net.
                </p>
            `
        },
        {
            text: "Qu es una consola? Cmo conseguirlo y dnde est Lamar?",
            answer: `
                <p class="accountmenu__content__cards__answer__text">
                La consola es una aplicacin dedicada a la piratera. Debe obtener la consola de Lamar, que est en el Ghetto (marcado en el mapa como un hombre amarillo).
                </p>
            `
        },
        {
            text: "Cmo encontrar mafia, pandilla o cualquier otra organizacin criminal?",
            answer: `
                <p class="accountmenu__content__cards__answer__text">
                Las pandillas estn marcadas con conos de calaveras en el mapa del Ghetto. Las mafias y organizaciones criminales no oficiales estn ocultas en el mapa. Su ubicacin se puede encontrar a travs de otros jugadores.
                </p>
            `
        },
        {
            text: "Qu es la rueda de la fortuna y qu cae all?",
            answer: `
                <p class="accountmenu__content__cards__answer__text">
                La Rueda de la Fortuna es una rueda de ruleta que se puede jugar todos los das reales despus de jugar solo 3 horas en el servidor. La rueda de la fortuna est disponible en el casino, entre las ganancias hay mscaras raras, un auto caro o un VIP.
                </p>
            `
        },
        {
            text: "Cmo puedo saciar mi sed y mi hambre?",
            answer: `
                <p class="accountmenu__content__cards__answer__text">
                Compra alimentos y bebidas a las 24/7. Si no satisfaces tus necesidades a tiempo, entonces tu personaje comenzar a caer y eventualmente perder el conocimiento..
                </p>
            `
        },
        {
            text: "Cmo conseguir un trabajo en cualquier organizacin del estado?",
            answer: `
                <p class="accountmenu__content__cards__answer__text">
                En total hay 5 organizaciones estatales: LSPD, BCSD, FIB, EMS, USMC, Life Invader y Gobierno. Para conseguir un trabajo en uno de ellos, debe ir al edificio principal de la organizacin (marcado en el mapa) y contactar a uno de los empleados y averiguar los detalles de la entrada.
                <br>
                <br>
                <ol>
                <li class="accountmenu__content__cards__answer__li">LSPD (Departamento de Polica) - Comprometido con la ley y el orden de nuestra ciudad</li>
                <li class="accountmenu__content__cards__answer__li">BCSD (Departamento del Sheriff) - Manejar la aplicacin de la ley del condado de Blaine</li>
                <li class="accountmenu__content__cards__answer__li">GOB (Gobierno) - Financiamiento de las organizaciones estatales, dictmenes y redaccin de leyes</li>
                <li class="accountmenu__content__cards__answer__li">EMS (Hospital) - Cuestiones de salud, seguro mdico, trata a las personas y elimina los accidentes</li>
                <li class="accountmenu__content__cards__answer__li">FIB (Oficina Federal de Investigaciones) - Controla los rganos estatales y su rden</li>
                <li class="accountmenu__content__cards__answer__li">USMC (Ejrcito) - Se ocupa del abastecimiento y la seguridad del Estado</li>
                <li class="accountmenu__content__cards__answer__li">Life Invader (Noticias): escribe noticias, presenta eventos y edita anuncios</li>
                </ol>
                </p>
            `
        },
    ]

    const [active, setActive] = useState(0)

    return (
        <React.Fragment>
            <div className="accountmenu__content__cards__item question accountmenu__scrollable with-margin">
                <div className="accountmenu__content__cards__container">
                    <span className="accountmenu__content__cards__title_single">
                        Preguntas frecuentes
                    </span>
                    <div className="accountmenu__cards__question__container">
                        {questions.map((item, index) => (
                            <Question
                                text={item.text}
                                key={index.toString()}
                                index={index}
                                active={active}
                                setActive={() => setActive(index)}
                            />
                        ))}
                    </div>
                </div>
            </div>
            <div className="accountmenu__content__cards__bitem accountmenu__scrollable">
                <div className="accountmenu__content__cards__container answer">
                    <div
                        className="accountmenu__content__cards__answer__img"
                    />
                    <div className="accountmenu__cards__question__icon__container__active">
                        <img src={`https://ciudadlatina.cc/client/images/icons/components/accountmenu/img/questio_icon.svg`} className="accountmenu__cards__question__icon" />
                        <span className="accountmenu__content__cards__answer__title">
                            {questions[active].text}
                        </span>
                    </div>
                    <div
                        className="accountmenu__content__cards__answer__text__container" 
                        dangerouslySetInnerHTML={{__html: questions[active].answer}}
                    />
                </div>
            </div>
        </React.Fragment>
    )
}

export default Faq